import { postUserImgUpload, postUserHeadImg, postUpdateNickname } from '@/api/userApi.js'
import T from '@/utils/tips.js'
export default {
	data() {
		return {
			headImgUrl: '../../../static/img/icon-user.png',
			person: {
				nickName: '',
				phone: '',
				address: ''
			}
		};
	},
	onLoad() {

	},
	onShow() {
		this.person.phone = uni.getStorageSync('phone')
		this.headImgUrl = uni.getStorageSync('headImgUrl') !== 'null' ? uni.getStorageSync('headImgUrl') :
			'/static/images/icon-user.png'
		// .substring(1, 2)
		let nickName = uni.getStorageSync('nickName')
		this.person.nickName = nickName === 'null' ? '' : uni.getStorageSync('nickName')
	},
	methods: {
		// 去添加地址
		goAddlist(){
			uni.navigateTo({
				url:'/pages/user/addlist/addlist'
			})
		},
		// 去修改昵称
		goNickName(){
			uni.navigateTo({
				url:'/pages/user/nickname/nickname'
			})
		},
		// 上传头像
		onUpload(e) {
			














			

			let _this = this
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera'], //从相册选择
			    success: function (res) {
					const tempFilePaths = res.tempFilePaths;
					let url = uni.getStorageSync('s') == '开发' ? 'http://192.168.0.202:8000/upms/userImg/upload' : '/upms/userImg/upload'
					uni.uploadFile({
						url: url, //仅为示例，非真实的接口地址
						filePath: tempFilePaths[0],
						name: 'file',
						success: (uploadFileRes) => {
							console.log(uploadFileRes.data);
							let res = JSON.parse(uploadFileRes.data)
							if (res.code === '1000') {
								_this.uploadUserHeadImg(res.data)
							} else {
								T.tips(res.message || '上传图片失败')
							}
						},
						fail:(err) => {
							T.tips('上传图片失败')
						}
					});
			    }
			});  

			

		},
		uploadUserHeadImg(url) {
			let data = {
				headImage: url
			}
			postUpdateNickname(data).then(res => {
				if (res.code === '1000') {
					this.headImgUrl = url
					localStorage.setItem('headImgUrl', url)
					T.tips(res.message || '头像上传成功')
				} else {
					T.tips(res.message || '上传图片失败')
				}
			}).catch(err => {
				T.tips(err.message || '上传图片失败')
			})
		}
	}
}
